<template>
  <div class="container">
    <div class="input-group mb-3">
      <div class="input-group-prepend">
        <span class="input-group-text" id="basic-addon1">待辦事項</span>
      </div>
      <input
        type="text"
        class="form-control"
        placeholder="準備要做的任務"
        v-model="newtodo"
        @keyup.enter="addtodo"
      >
      <div class="input-group-append">
        <button class="btn btn-primary" type="button" @click="addtodo">新增</button>
      </div>
    </div>
    <div class="card text-center">
      <div class="card-header">
        <ul class="nav nav-tabs card-header-tabs">
          <li class="nav-item">
            <a
              class="nav-link"
              :class="{'active': visibility == 'all'}"
              @click="visibility = 'all'"
              href="#"
            >全部</a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              :class="{'active': visibility == 'active'}"
              @click="visibility = 'active'"
              href="#"
            >進行中</a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              :class="{'active': visibility == 'completed'}"
              @click="visibility = 'completed'"
              href="#"
            >已完成</a>
          </li>
        </ul>
      </div>
      <ul class="list-group list-group-flush text-left">
        <li class="list-group-item" v-for="(item, key) in filterTodos" @dblclick="edittodo(item)">
          <div class="d-flex" v-if="item.id !== catchtodo.id">
            <div class="form-check">
              <input
                type="checkbox"
                class="form-check-input"
                v-model="item.completed"
                :id="item.id"
              >
              <label
                class="form-check-label"
                :class="{'completed': item.completed}"
                :for="item.id"
              >{{ item.title }}</label>
            </div>
            <button
              type="button"
              class="close ml-auto"
              @click="removetodo(item)"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <input
            type="text"
            class="form-control"
            v-model="catchtitle"
            @keyup.esc="cancelEdit()"
            @keyup.enter="doneEdit(item)"
            v-if="item.id === catchtodo.id"
          >
        </li>
      </ul>

      <div class="card-footer d-flex justify-content-between">
        <span>還有{{numofUncompleted}}筆任務未完成</span>
        <a href="#" @click.prevent="clearAll">清除所有任務</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newtodo: "",
      todos: [
        {
          id: "",
          title: "",
          completed: false
        }
      ],
      catchtodo: {},
      catchtitle: "",
      visibility: "all",
      numofUncompleted: 0
    };
  },
  methods: {
    addtodo: function() {
      var value = this.newtodo.trim(); //trim() =>刪除多於空格
      var timestamp = Math.floor(Date.now());
      if (!value) {
        //沒數據按新增無法新增項目
        return;
      }
      this.todos.push({
        id: timestamp,
        title: value,
        computed: false
      });
      this.newtodo = "";
      this.numofUncompleted = this.numofUncompleted + 1;
    },
    removetodo: function(todo) {
      var vm = this;
      var newIndex = vm.todos.findIndex(function(item, key) {
        return todo.id == item.id;
        if (todo.completed === false) {
          this.numofUncompleted = this.numofUncompleted - 1;
        }
      });
      this.todos.splice(newIndex, 1); //splic 刪除陣列,1為刪除一筆
    },
    edittodo: function(item) {
      this.catchtodo = item;
      this.catchtitle = item.title;
    },
    cancelEdit: function() {
      this.catchtodo = {};
    },
    doneEdit: function(item) {
      item.title = this.catchtitle;
      this.catchtitle = "";
      this.catchtodo = {};
    },
    countUncomplished: function(item) {
      if (item.completed === true) {
        this.numofUncompleted = this.numofUncompleted + 1;
        return;
      } else {
        this.numofUncompleted = this.numofUncompleted - 1;
      }
    },
    clearAll: function() {
      this.todos = [];
      this.numofUncompleted = 0;
    }
  },
  computed: {
    filterTodos: function() {
      if (this.visibility == "all") {
        return this.todos;
      } else if (this.visibility == "active") {
        var newTodos = [];
        this.todos.forEach(function(item) {
          if (!item.completed) {
            newTodos.push(item);
          }
        });
        return newTodos;
      } else if (this.visibility == "completed") {
        var newTodos = [];
        this.todos.forEach(function(item) {
          if (item.completed) {
            newTodos.push(item);
          }
        });
        return newTodos;
      }
    }
  }
};
</script>
<style>
  @import url(./CSS/all.css);
.completed {
  text-decoration: line-through;
}
</style>